.navbar {
    width: 100vw;
    background-color: white;
    position: fixed;
    box-shadow: 1px 1px 1px 1px  rgb(221, 221, 221);

    /* margin-bottom: -300px; */
    



}

hr{
    margin-bottom: 50px;
}







.content {
    /* background-color: green; */
    display: flex;
    width: 20%;
    align-items: center;
    justify-content: space-around;
  
}



.round_img {
    width: 30px;
    height: 30px;
    border-radius: 50%;
}


